
  .banner img{
    border-radius: 10px;
    width: 100%;
    max-width: 1080px;
    background-repeat: no-repeat;
    display: flex;
    border-radius: 10px;
    object-fit:
      cover;
  }
  


  @media (max-width:900px){
    .banner img {
      max-width: 700px;
      display: flex;
    }
  }